/* eslint-disable react/jsx-key */
/* eslint-disable react/prop-types */
import { Button,Card, Stack, Grid,  CardContent,Divider,Box, Typography } from '@mui/material';
import { alpha, useTheme, styled } from '@mui/material/styles';
import Iconify from '../../../../components/Iconify';



const IconWrapperStyle = styled('div')(({ theme }) => ({
  width: 60,
  height: 60,
  display: 'flex',
  borderRadius: '50%',
  padding:5,
  alignItems: 'center',
  justifyContent: 'center',
  color: theme.palette.success.main,
  backgroundColor: alpha(theme.palette.success.main, 0.16),
}));


export default function Profile({data}) {
  const theme = useTheme();
  console.log(data)
  return (
    <Grid container spacing={3}>
      <Grid item xs={12} md={4}>
        <Stack spacing={3}>
        <Card sx={{ py: 3 }}>
      <Stack direction="row" divider={<Divider orientation="vertical" flexItem />}>
        <Stack width={1} textAlign="center">
          <Typography variant="h5">{data?.hp_formula}</Typography>
          <Typography variant="body2" sx={{ color: 'text.secondary' }}>
            HP Formula
          </Typography>
        </Stack>

        <Stack width={1} textAlign="center">
          <Typography variant="h5">{data?.atk_formula}</Typography>
          <Typography variant="body2" sx={{ color: 'text.secondary' }}>
          Attack Formula
          </Typography>
        </Stack>
      </Stack>

          </Card>
          <Card sx={{ py: 3,mt:3 }}>
          <Stack direction="row" divider={<Divider orientation="vertical" flexItem />}>
            <Stack width={1} textAlign="center">
              <Typography variant="h5">{data?.g_hp_modifier}</Typography>
              <Typography variant="body2" sx={{ color: 'text.secondary' }}>
                HP Modifier
              </Typography>
            </Stack>
    
            <Stack width={1} textAlign="center">
              <Typography variant="h5">{data?.g_atk_modifier}</Typography>
              <Typography variant="body2" sx={{ color: 'text.secondary' }}>
                Attack Modifier
              </Typography>
            </Stack>
          </Stack>
            </Card>
        </Stack>
      </Grid>

      <Grid item xs={12} md={4}>
        <Stack spacing={3}>
        <Card>
        <CardContent>
        <Stack spacing={2}>
          <Stack direction="row" justifyContent="space-between">
            <Typography variant="body2" sx={{ color: 'text.secondary' }}>
             Magic type
            </Typography>
            <Typography variant="subtitle2">{data?.magic_type?.name}</Typography>
          </Stack>
          <Divider />
          <Stack direction="row" justifyContent="space-between">
            <Typography variant="body2" sx={{ color: 'text.secondary' }}>
            Rarity
            </Typography>
            <Typography variant="subtitle2">{data?.rarity?.name}</Typography>
          </Stack>
        </Stack>
      </CardContent>
        </Card>




      

        </Stack>
      </Grid>

      <Grid item sx={12} md={4}>
      <Stack spacing={3}>
      <Card sx={{ display: 'flex', alignItems: 'center', p: 3 }}>
      <Box sx={{ flexGrow: 1 }}>
        <Typography  variant="h4">TAG</Typography>

        <Stack direction="row" alignItems="center" spacing={1} sx={{ mt: 2, mb: 2 }}>
          <Typography component="span" variant="subtitle2">
          Total
          </Typography>
          <Typography component="span" variant="subtitle2">
        {data?.card_tag?.length}
          </Typography>
        </Stack>
        {data?.card_tag?.map(e=><Button  variant="outlined"  sx={{ borderRadius: '20px', marginRight: '4px',marginBottom:'2px' }} >{e?.tag?.name}</Button>)}
        
      </Box>
      <IconWrapperStyle
      sx={{
        
          color: 'secondary',
          bgcolor: alpha(theme.palette.error.main, 0.16),

      }}
    >
    <Iconify
    width={86}
    height={86}
    icon='solar:tag-linear'
  />
    </IconWrapperStyle>
    </Card>
      </Stack>
      </Grid>
    </Grid>
  );
}
